.section-skill {
    padding: 50px 0;

    @include media('≥tablet') {
        padding: 100px 0;
    }
    .title {
        margin-bottom: 25px;
        text-align: center;

        @include media('≥tablet') {
            margin-bottom: 50px;
            text-align: left;
        }
    }

    .skill {
        &-row {

            @include media('≥tablet') {
                display: flex;
                align-items: center;
                margin: 0 -75px;
            }
        }
        &-col {

            @include media('≥tablet') {
                width: 50%;
                padding: 0 75px;
            }
            &--img {
                text-align: center;
            }
        }
        &-item {
            margin-bottom: 30px;
        }
        &-name {
            display: inline-block;
            margin-bottom: 20px;
        }
        &-load {
            background: #C4C4C4;
            height: 4px;
            position: relative;
            overflow: hidden;
        }
        &-fill {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            background: $blue;
        }
    }
}