.section-software {
    padding: 35px 0 50px;

    @include media('≥tablet') {
        padding: 72px 0 100px;
    }
    .software-holder {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;

        @include media('≥tablet') {
            
        justify-content: space-between;
        }
    }
    .software-item {
        max-width: 255px;
        flex-basis: 50%;

        @include media('≥tablet') {
            flex-basis: 25%;
        }
    }
}