#footer {
  background: $base-background-footer;
  padding: 25px 0;
  color: $white;

  @include media('≥tablet') {
    padding: 50px 0;
  }
  .footer-holder {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include media('≥tablet') {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  .name-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;

    @include media('≥tablet') {
      margin-bottom: 0;
      align-items: flex-start;
    }
  }
  .name {
    font-size: 21px;
    line-height: 1.2;
    font-weight: 500;
    margin-bottom: 10px;

    @include media('≥tablet') {
      margin-bottom: 10px;
    }
  }
  .link-box {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
  }
  .link-item {
    padding: 0 10px;
  }
}