.section-work {
    padding: 50px 0;
    text-align: center;
    background-color: $light-gray;

    @include media('≥tablet') {
        padding: 100px 0;
    }
    .title {
        margin-bottom: 25px;

        @include media('≥tablet') {
            margin-bottom: 50px;
        }
    }
    .holder {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .holder-info {
        margin-bottom: 30px;

        @include media('≥tablet') {
            margin-bottom: 60px;
            max-width: 540px;
        }
    }
    .work-img {
        padding: 0 15px;
    }
}