.section-projects {
    background: $blue;
    padding: 50px 0 0;
    color: $white;

    @include media('≥tablet') {
        padding: 100px 0 50px;
    }

    .list {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -25px;
    }

    .col {
        width: 50%;
        flex-basis: 50%; 
        padding: 0 15px 50px;

        @include media('≥tablet') {
            width: 33.333%;
            flex-basis: 33.333%;
        }
        @include media('≥desktop') {
            width: 16.6%;
            flex-basis: 16.6%;
        }
    }

    .item {
        display: flex;
        align-items: center;
    }
    .icon-box {
        margin-right: 10px;

        @include media('≥tablet') {
            margin-right: 20px;
        }
    }
    .text-box {
        display: flex;
        flex-direction: column;
        max-width: 71px;
    }
    .title {
        font-weight: 400;
        color: $white;
        margin-bottom: 0;
    }
}