.section-contacts {
    padding: 50px 0;
    background-color: $light-gray;

    @include media('≥tablet') {
        padding: 100px 0;
    }
    .contacts-holder {
        display: flex;
        flex-direction: column;
        max-width: 540px;
        margin: 0 auto;
    }
    .contacts-text {
        text-align: center;
        margin-bottom: 15px;

        @include media('≥tablet') {
            margin-bottom: 45px;
        }
    }
    .contacts-form {
        display: flex;
        flex-direction: column;
    }
    .form-row {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-bottom: 20px;

        input {
            width: 100%;
            height: auto;
            border: none;
            padding: 17px 0 17px 20px;
            margin-bottom: 10px;

            @include media('≥tablet') {
                margin-bottom: 0;
                width: 255px;
            }
        }
    }
    .comments-area {
        max-height: 175px;
        margin-bottom: 20px;
        resize: none;
        border: none;
        color: $gray;
        padding: 17px 0 17px 20px;

        @include media('≥tablet') {
            margin-bottom: 50px;
        }
    }
    .form-btn {
        height: auto;


        @include media('≥tablet') {
            width: 33.33%;
            align-self: center;
        }
    }
}