.section-banner {
	

	background-size: cover;
	background-position: center;
	padding: 80px 0 0;
	background-color: $light-gray;
	min-height: 600px;

	@include media('≥tablet') {
        padding: 20px 0;

		@include v-center(700px);
    }

	.df-row {
		.col-sm-6 {
			padding: 0 30px;

			@include media('≥tablet') {
				padding: 0 65px;
			}
		}

		@include media('≥tablet') {
			margin: 0 -65px;
		}
	}

	.banner-img {
		display: flex;
		align-items: center;
		justify-content: center;

		@include media('≥tablet') {
			justify-content: flex-end;
		}
	}
	.banner-text {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding-top: 20px;

		@include media('≥tablet') {
			padding-top: 0;
			align-items: flex-start;
		}

		.title {
			margin-bottom: 20px;

			@include media('≥tablet') {
			margin-bottom: 60px;
			}
		}
		p {
			margin-bottom: 20px;
			text-align: center;

			@include media('≥tablet') {
			margin-bottom: 60px;
			text-align: left;
			}
		}

		.btn {
			padding: 15px 21px;
			text-align: center;
			max-width: 100%;

			@include media('≥tablet') {
				padding: 25px 41px;
			}
		}
	}	
}